*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    place-items: center;
    gap: 20px;
    width: 100vw;
    height: 100vh;
    background-color: green;
    overflow: hidden;
}

.playerName{
    font-weight: bolder;
    text-align: center;
    padding: 5px 0px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.deck{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    padding: 20px;
    flex-direction: row-reverse;
    background-color: rgb(5, 84, 5);
    border-radius: 10px;
}

.placar{
    background-color: rgb(85, 165, 77);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 10px;
}

.buttons{
    display: flex;
    justify-content: center;
    gap: 10px;
    flex-direction: column;
}

.buttons button{
    border-radius: 10px;
    background-color: white;
    font-weight: bolder;
    box-shadow: 2px 2px 2px rgb(5, 36, 5);
    outline: none;
    border: none;
    height: 40%;
    width: 100%;
}

.p-pONE, .p-pTWO{
    display: flex;
    gap: 10px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}


#placarid{
    text-align: center;
    font-weight: bolder;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

@media (max-width: 420px){

    .container{
        height: 100vh;
        overflow: hidden;
    }
    .deck{
        place-items: center;
        width: 100vw;
        height: 200px;
    }

    .deck img{
        width: 70px;
    }

    .placar{
        height: 100px;
        font-size: 10px;
        font-weight: bolder;
    }

    .deck button{
        height: 70px;
        width: 70px;
        border-radius: 100%;
        outline: none;
        border: none;
    }
        .box img{
        width: 100px;
    }
}